.product{
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    flex-wrap:wrap ;
    gap:20px;

    margin-top: 10px;
    background-color: blueviolet;

}
.productcon{
    width: 30%;
    justify-content: space-evenly;
    flex-direction: column;
    display: flex;
    align-items: center;
    background-color: rgb(153, 113, 65);
}
.prdimg{
    width: 100px;
    height: 100px;
    border: 2px solid peru;
    border-radius: 10px;
}
.prdimg img{
    width: 100%;
    height: 100%;
    border: 2px solid peru;
    border-radius: 10px;
    
}
.productcon p{
    text-align: center;
    width: 100%;
    font-size: 25px;
    line-break: anywhere;

}
.productcon:nth-child(even){
    background-color: pink;
}