.test{
    width: 30%;
    margin-top: 80px;
    background-color: violet;
    height: 30vh;
    left: 50%;
    transform: translateX(-50%);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    gap:20px;
}
.testimg{
    width:100px;
    height: 100px;
    position: absolute;
    top:-50px;
    border: 2px solid palevioletred;
    border-radius: 50%;
}
.testimg img{
    width: 100%;
    height: 100%;
    border: 2px solid palevioletred;
    border-radius: 50%;
}
.test p{
    width: 100%;
    text-align: center;
    font-size: 25px;
}